import {
	getDataTractionResponse	
} from '@/config/incubator/traction'

const dataDashboard = {
	namespaced: true,

	state: {
		dataStartup: [],
		dataRespon: [],
		dataTraction: null
	},

	mutations: {
		updateDataStartup(state, payload) {
			state.dataStartup = payload
		},
		updateDataTraction(state, payload) {
			state.dataTraction = payload
		},
		updateDataRespon(state, payload) {
			state.dataRespon.push(payload)
		}
	},

	actions: {
		updateDataTraction(context, payload) {
			context.commit('updateDataTraction', payload)
		},
		updateDataStartup(context, payload) {
			context.commit('updateDataStartup', payload)
		},
		updateDataRespon(context, payload) {
				payload.map((x) => {
					getDataTractionResponse({
						traction_id: x.traction_id,
						startup_id: x.id
					})
						.then(res => {
							let data = {
								data: res.data.data[0].detail,
								startup_id: x.id
							}
							context.commit('updateDataRespon', data)							
						})							
				})			
		}
	}
}

export default dataDashboard