import Vue from 'vue'
import Vuex from 'vuex'

import dataLogin from './login'
import dataRegister from './register'

import dataIncubator from './admin/incubator'
import dataStartup from './admin/startup'

import dataIncubatorStartup from './incubator/startup'
import dataIncubatorIncubator from './incubator/incubator'
import dataIncubatorMentor from './incubator/mentor'
import dataIncubatorAward from './incubator/award'
import dataIncubatorTenant from './incubator/tenant'
import dataIncubatorTraction from './incubator/traction'
import dataIncubatorDashboard from './incubator/dashboard'

import dataStartupAward from './startup/award'
import dataStartupFounder from './startup/founder'
import dataStartupProduct from './startup/product'
import dataStartupStartup from './startup/startup'
import dataStartupTraction from './startup/traction'
import dataStartupSprint from './startup/sprint'
import dataStartupDashboard from './startup/dashboard'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		modalToken: false
	},
	mutations: {
		updateModalToken (state, payload) {
      state.modalToken = payload
    }
	},
	actions: {
		updateModalToken (context, payload) {
      context.commit('updateModalToken', payload)
    }
	},
	modules: {
		dataStartupDashboard: dataStartupDashboard,
		dataStartupSprint: dataStartupSprint,
		dataStartupTraction: dataStartupTraction,
		dataStartupStartup: dataStartupStartup,
		dataStartupProduct: dataStartupProduct,
		dataStartupAward: dataStartupAward,
		dataStartupFounder: dataStartupFounder,
		dataIncubatorDashboard: dataIncubatorDashboard,
		dataIncubatorTraction: dataIncubatorTraction,
		dataIncubatorTenant: dataIncubatorTenant,
		dataIncubatorAward: dataIncubatorAward,
		dataIncubatorMentor: dataIncubatorMentor,
		dataIncubatorIncubator: dataIncubatorIncubator,
		dataIncubatorStartup: dataIncubatorStartup,
		dataStartup: dataStartup,
		dataIncubator: dataIncubator,
		dataRegister: dataRegister,
		dataLogin: dataLogin
	}
})
